import  React,{Component} from "react"
import  Cell from "../components/Cell/Cell"
import ReactSwipe from 'react-swipe';
import "../assets/css/home.css"
import collct from "../assets/img/collct.png"
import Ali from "../components/ali/Ali"
import Header from "../layouts/Header"
import Foot from "../layouts/Foot"
import axios from "../utils/axios";
export default class Home extends Component{
    state={
        list:[]
    };
    componentDidMount(){
        axios({
            url:"/api/list",
            params:{_limit:10}
        }).then(
            res=>this.setState({list:res.data.data})
        )
    }
 render(){
     let {list} = this.state
    
        return (
            
                    <div className="Home">
                        <Header></Header>
                        <ReactSwipe
                            className="carousel"
                            swipeOptions={{ 
                                auto: 1000, 
                                speed: 100,
                            }}
                           
                        >
                            <div style={{background:"yellow"}}>PANE 1</div>
                            <div style={{background:"green"}}>PANE 2</div>
                            <div style={{background:"blue"}}>PANE 3</div>
                        </ReactSwipe>
                    <Cell></Cell>
                    <section style={{backgroundColor:"#F2F2F2"}}>
                        <p className="title"><img src={collct}/>&nbsp;精品</p>
                            <div style={{width: "100%"}}>
                                <ul className="plist">
                                    {
                                        list.map((item,index)=>(
                                            <Ali
                                            key={index}
                                            data={item}
                                            
                                            />
                                        ))
                                    }
                                        
                                </ul>
                            </div>
		            </section>
                    <Foot/>
                    </div>
                )



 }
} 