import  React,{Component} from "react"
import "./css/ali.css"
import {Link} from "react-router-dom"
export default class Ali extends Component{
    render(){
	let {data} = this.props
		return(
			
			<Link to={"/content/"+data._id}>
							<li>
								<img src={"http://localhost:3000"+this.props.data.ico} style={{height:"120px",width:"100%"}} />
								<p className="name">{this.props.data.title}</p>

								<div>
									<span style={{fontSize: "14px",color: "red"}}>￥200</span>
									<span style={{float: "right"}}>购买</span>
								</div>
							</li>
			</Link>
		)
	}
}