import React from "react"
import {Link} from "react-router-dom"
import "../assets/css/reg.css"
import "../assets/css/app.css"
import "../assets/css/bass.css"
import axios from "../utils/axios";
class Reg extends React.Component{
    state={
        data:"",
        data2:"",
        data3:"",
        data4:"",
    }
    add=()=>{
        let fromData= new FormData();
        fromData.append("username",this.state.data)
        fromData.append("password",this.state.data2)
        fromData.append("ico",this.refs.f1.files[0])
        axios({
            url:"/api/reg",
            method:"post",
            data:fromData
        }).then(
            res=>console.log(res)//this.props.history.push("/login")
        )
    }
    
        
    render(){
        return (
            <div>
               <header>
                    <div className="navtitle">
                        <a href="javascript:history.back(-1)">
                            <span style={{float:"left",color:"#fff"}}><i className="icon-href">&#x21;</i>						
                            </span>
                        </a>
                        <span className="n-title">注册</span>
                        <a href="javascript:;"><span style={{float:"right",marginRight:"10px"}}>
                            <i className="icon-href" style={{color:"#fff"}}>&#xe009;</i>
                        </span>
                    </a>
                    </div>
                </header>

                <div id="content">
                    <div className="content_m">
                        <form id="reg" >
                            
                                <div className="login_m">
                                    <div className="l_box">
                                        <div className="l_box_l">手机/邮箱</div>
                                        <div className="l_box_r">
                                            <input type="text" id="username" name="username" onChange={(ev)=>{this.setState({data:ev.target.value})}} />
                                        </div>
                                        <div className="clear"></div>
                                    </div>
                                    <div className="l_box">
                                        <div className="l_box_l">密&nbsp;　　码</div>
                                        <div className="l_box_r">
                                            <input type="password" id="password" name="password" onChange={(ev)=>{this.setState({data2:ev.target.value})}}/>
                                        </div>
                                        <div className="clear"></div>
                                    </div>
                                    <div className="l_box">
                                        <div className="l_box_l" style={{verticalAlign:"top"}} >确认密码</div>
                                        <div className="l_box_r">
                                            <input type="password" id="password2" name="password2" onChange={(ev)=>{this.setState({data3:ev.target.value})}}/>
                                        </div>
                                        <div className="clear"></div>
                                    </div>
                                    <div className="l_box">
                                        <div className="l_box_l" style={{verticalAlign:"top"}} >昵称</div>
                                        <div className="l_box_r">
                                            <input type="password" id="password2" name="password2" onChange={(ev)=>{this.setState({data4:ev.target.value})}}/>
                                        </div>
                                        <div className="clear"></div>
                                    </div>
                                    <div className="l_box" >
                                        <div className="l_box_l" style={{verticalAlign:"top"}} >头像</div>
                                        <div className="l_box_r" style={{height:"5em"}}>
                                            <input type="file" ref="f1" id="password2" name="password2" />
                                        </div>
                                        <div className="clear"></div>
                                    </div>
                                </div>
                                <button className="login_btn_n" type="button" onClick={this.add}>注册</button>
                                <p className="b_log">已有账号请 <Link to="/Login" style={{color:"#ed0122"}} >点此登录</Link></p>
                        </form>    
                    </div>
                </div>


            </div>
        )
    }
}

export default Reg