import  React,{Component} from "react"
import dsp04  from "../assets/img/d_sp_04.png"
import axios from "../utils/axios";
export default class Shopcar extends Component{
    state={
        list:[]
    }
    componentDidMount(){
		let a = localStorage.getItem("shouji")
		axios({
            
            url:"/api/cart?user_id="+a
          
		}).then(
			res=>this.setState({list:res.data.data})
		)
	}
    render(){
        let {list} = this.state
           return (
            <div style={{backgroundColor: "#F2F2F2"}}>		
                   <header>
                <div className="navtitle">
                    <a href="javascript:history.back(-1)">
                        <span style={{float:"left"}}><i className="icon-href">&#x4a;</i>						
                        </span>
                    </a>				
                    <span className="n-title">购物车</span>
                    <a href="index.html"><span style={{float: "right",marginRight: "10px"}}>
                        <i className="icon-href">&#xe009;</i>
                    </span>
                </a>
                </div>
            </header>
            <section style={{marginTop: "45px"}}>
                <ul className="plist2" style={{paddingBottom:"50px"}}>
                    {list.map((item,index)=>(
                        <li key={index}> 				 					
                        <div className="conImg"><img src={"http://localhost:3000"+item.ico}  style={{width:"80px",height:"80px"}} /></div>
                        
                        <div className="content">
                           <p>{item.kuanshi}</p> 
                           <p style={{color: "red", fontSize: "16px"}}>{item.newprice}</p>
                               <div className="num">
                                   <button className="numjian">-</button>
                                   <input type="text" value="1" className="numtxt" style={{marginBottom: 0}} />
                                   <button className="numjia">+</button>
                                   <i className="icon-href dele">&#xe019;</i>
                               </div>
                       
                        </div> 				
                   </li>
                    ))}
              
                     
             </ul>
            </section>
            <section>
                <div className="btngroup">
                    <div style={{display: "inline-block"}}>					
                        共购买 12 件，总计：<span className="redfont" style={{fontWeight: "bold"}} >4.00元</span>
                    </div>
                    <a href="jiesuan.html"><button className="tip-btn orange" style={{width: "23%"}}>去结算</button></a>		
                </div>
            </section>
            </div>      
                   )
    }
   }