import React,{Component} from "react"
import Foot from "../layouts/Foot"
import axios from "../utils/axios";
export default class User extends Component{
	add=()=>{
		axios({
			url:"/api/logout",
			method:"put",

		}).then(
			this.props.props.history.push("/login")
			).then(
			localStorage.setItem("shouji",null)	
			)
	}
	
    render(){
		
        return(

            <div style={{backgroundColor:"#F2F2F2"}}>
		<header>
			<div className="navtitle">
				<a href="javascript:history.back(-1)">
					<span style={{float:"left"}}><i className="icon-href">&#x21;</i>						
					</span>
				</a>
				<span className="n-title">个人中心</span>
				<a href="index.html"><span style={{float:"right",marginRight: "10px"}}>
					<i className="icon-href">&#xe009;</i>
				</span>
			</a>
			</div>
		</header>
		<section>
		<div style={{backgroundColor:"#FFFFFF",padding: "15px",marginTop:"40px"}}>
			<ul className="userinfo">
				<li>
					<img src={"http://localhost:3000"+this.props.data.icon} />
				</li>
				<li>
					<p style={{fontSize:"16px"}}>{this.props.data.username}</p>
				</li>
				<button className="tip-btn red" style={{width:"3rem"}} onClick={this.add}>注销</button>
			</ul>
			<ul className="moneyinfo">
				<li>
					<p style={{color: "#999"}}>可用积分</p>
					<p className="redfont">20分</p>
				</li>
				<li>
					<p style={{color: "#999"}}>可用余额</p>
					<p className="redfont">￥0.00</p>
				</li>
				<li>
					<button className="tip-btn red" style={{width:"55%"}}>去充值</button>
				</li>
			</ul>			
		</div>		
		<ul className="menulist">		
			<a href="orderList.html">
				<li style={{marginBottom: "10px"}}><i className="icon-end">&#x68;</i>
					<span>全部订单</span>
					<i className="icon-end" style={{float:"right"}}>&#x35;</i>
				</li>
			</a>
			<a href="duobao.html">
				<li>
					<i className="icon-end">&#xe0ed;</i>
					<span>待付款</span>
					<i className="icon-end" style={{float:" right"}}>&#x35;</i>
					<em>5</em>
				</li>
			</a>
			<a href="zhongjinag.html">
				<li>
					<i className="icon-end">&#x7d;</i>
					<span>代发货</span>
					<i className="icon-end" style={{float:" right"}}>&#x35;</i>
					<em>5</em>
				</li>
			</a>
			<a href="#">
				<li><i className="icon-end">&#xe023;</i>
					<span>已发货</span>
					<i className="icon-end" style={{float:" right"}}>&#x35;</i>
					<em>5</em>
				</li>
			</a>
			<a href="#">
				<li><i className="icon-end">&#xe030;</i>
					<span>已完成</span>
					<i className="icon-end" style={{float:" right"}}>&#x35;</i>
				</li>
			</a>
			<a href="chongzhi.html">
				<li style={{margin:"10px 0"}}><i className="icon-end">&#xe0ed;</i>
					<span>充值记录</span>
					<i className="icon-end" style={{float:" right"}}>&#x35;</i>
				</li>
			</a>
			<a href="#">
				<li style={{margin:"10px 0"}}><i className="icon-end">&#xe01d;</i>
					<a href="address.html"><span>地址管理</span></a>
					<i className="icon-end" style={{float:" right"}}>&#x35;</i>
				</li>
			</a>
		</ul>
		</section>
		<Foot></Foot>
	</div> 
        )
    }
}