import React from "react"
import {Link} from "react-router-dom"
import "../assets/css/reg.css"
import "../assets/css/app.css"
import "../assets/css/bass.css"
import axios from "../utils/axios";

class Login extends React.Component{
    state={
        data:"",
        data2:""
    }
 add=()=>{
        axios({
            url:"/api/login",
            method:"post",
            data:{username:this.state.data,password:this.state.data2,save:"true"}
        }).then(
            res=>{
                if(res.data.error == 1){
                    alert(res.data.mess)
                }else{
                    localStorage.setItem("shouji",res.data.data[0]._id)
                    this.props.history.push("/user")
                }
            }
        )
    }
    render(){
        return (
            <div>
                <header>
                    <div className="navtitle">
                        <a href="javascript:history.back(-1)">
                            <span style={{float:"left",color:"#fff"}}><i className="icon-href">&#x21;</i>						
                            </span>
                        </a>
                        <span className="n-title">登录</span>
                        <a href="javascript:;"><span style={{float:"right",marginRight:"10px"}}>
                            <i className="icon-href" style={{color:"#fff"}}>&#xe009;</i>
                        </span>
                    </a>
                    </div>
                </header>
                <div id="content">
                    <div className="content_m">
                        <form id="dologin" >
                       
                            <div className="login_m">
                                <div className="l_box">
                                    <div className="l_box_l">手机/邮箱</div>
                                    <div className="l_box_r">
                                        <input type="text" id="username" name="username" type="text" value={this.data}
                                        onChange={(ev)=>{this.setState({data:ev.target.value})}}/>
                                    </div>
                                    <div className="clear"></div>
                                </div>
                                <div className="l_box">
                                    <div className="l_box_l">密&nbsp;　　码</div>
                                    <div className="l_box_r"><input type="password" id="password" name="password"value={this.data2} onChange={(ev)=>{this.setState({data2:ev.target.value})}}/>
                                </div>
                                <div className="clear"></div>
                            </div>
                            </div>
                            <button className="login_btn_n" type="button" onClick={this.add}>登录</button>
                            <p className="b_log">您还没有美西账号？ <Link to="/reg" type="button" style={{color:"#ed0122"}}>立即注册</Link></p>
                            
                           
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default Login