import  React,{Component} from "react"
import dsp04 from "./img/d_sp_04.png"
import about from "./img/about.png"
import "./css/cell.css"
import axios from "../../utils/axios";
export default class Cell extends Component{
	state={
        list:[]
    };
    componentDidMount(){
        axios({
            url:"/api/list",
            params:{_limit:10}
        }).then(
            res=>this.setState({list:res.data.data})
        )
    }
 render(){
	let {list} = this.state
        return (	
            <section style={{backgroundColor: "#F2F2F2"}}>
			<p className="title"><img src={about} /> 爆品推荐</p>
			<div className="bpcontent" style={{overflowY:'hidden',overflow:'auto',whiteSpace:'nowrap'}}>
				<ul className="bplist">
						{
							list.map((item,index)=>(
								<li
								key={index}
								data={item}
								>
							<a href="content.html">
							<p><img src={"http://localhost:3000"+item.ico} style={{width:"90px",height:"90px"}} /></p>
							<p className="name">{item.title}</p>
							<p><span style={{fontSize: "13px", color: "red"}}>￥200</span></p>
						</a>
					</li>                   
							))
						}										
					{/* {<li>
					
						<a href="content.html"> */}
                            {/* {行内样式的图片是指在pubic下面} */}
							{/* <p><img src={dsp04} style={{width:"90px",height:"90px"}} /></p>
							<p className="name">全自动照相机</p>
							<p><span style={{fontSize: "13px", color: "red"}}>￥200</span></p>
						</a>
					</li>}
                     */}
				
				</ul>
			</div>
		</section>
                )
 }
}