import  React,{Component} from "react"
 import {Route,Switch,Redirect,} from "react-router-dom"
import  Header from "./Header"
import Foot from "./Foot"
import Home from "../pages/Home"
import Product from "../pages/Product"
import Content from "../pages/Content"
import User from "../pages/User"
import Shopcar from "../pages/Shopcar"
import ErrorPage from "../pages/ErrorPage"
import Login from "../pages/Login"
import Reg from "../pages/Reg"
import "../assets/css/public.css"
import Auth from "../guard/AuthUser"
import PubSub from 'pubsub-js'
import Loading from "../components/Loading/Loading";
export default class App extends Component{
 
  checkPath(path){
    if (/home/.test(path)){
      this.setState({bNav:true,bFoot:true})
    }
    if (/login|reg|product|shopcar/.test(path)){
      this.setState({bNav:false,bFoot:true})
    }
    if (/user/.test(path)){
      this.setState({bNav:false,bFoot:true})
    }
    if (/content/.test(path)){
      this.setState({bNav:false,bFoot:false})
    }
  }
 
  state={
    bLoading:false,
    bNav:false,
    bFoot:false
  }
  constructor(){
    super();
    this.token=PubSub.subscribe('VIEW_LOADING',(evname,payload)=>{
      console.log(evname,payload);
      this.setState({bLoading:payload})
    })
   
  }
  componentWillReceiveProps(nextProps){
    console.log('app',nextProps.location.pathname);//监听路由
    let path  = nextProps.location.pathname;
    this.checkPath(path)
  };
  componentDidMount(){
    console.log(this.props.location)
    let path  = this.props.location.pathname;
    this.checkPath(path)
  }
  
 render(){
  
        return (
                    <div className="App">
                        {this.state.bLoading && <Loading /> }
                        {this.state.bNav&& <Header /> }
                      <Switch>
                        <Route path="/home" component={Home} />
                        <Route path="/product" component={Product}/>
                        <Route path="/content/:id" component={Content}/>
                        <Auth path="/user" component={User}/>
                        <Route path="/shopcar" component={Shopcar}/>
                        <Route path="/login" component={Login}/>
                        <Route path="/reg" component={Reg}/>
                        <Redirect exact from="/" to="/home"></Redirect>  
                        <Route component={ErrorPage}></Route>         
                        </Switch>
                        {/* <Home></Home> */}
                        {this.state.bFoot&& <Foot /> }
                    </div>

                )
 }
}