import React from "react"

import "../assets/css/product.css"
import "../assets/css/app.css"
import "../assets/css/bass.css"
import axios from "../utils/axios";


class Product extends React.Component{
    state={
        list:[]
    };
    componentDidMount(){
        axios({
            url:"/api/home",
            params:{_limit:10}
        }).then(
            res=>this.setState({list:res.data.data})
        )
    }
    render(){
        let {list} = this.state
        console.log(list)
        return (
            <div  id="tabSlider2" className="tabSlider">
                <header>
                    <div className="navtitle">
                        <a href="javascript:history.back(-1)">
                            <span style={{float:"left"}}>
                                <i className="icon-href" style={{color:"#fff"}}>&#x4a;</i>						
                            </span>
                        </a>
                        <span className="n-title">商品列表</span>
                        <a href="javascript:;">
                            <span style={{float:"right",marginRight:"10px"}} >
                                <i className="icon-href"  style={{color:"#fff"}}>&#xe009;</i>
                            </span>
                        </a>
                </div>							
                <div style={{position:"fixed",top:"40px",width:"100%",backgroundColor:"white",zIndex:"1"}}  >
                    
                        <ul className="tabSlider-hd">
                            <li className="curr"><span>默认</span></li>
                            <li><span>销量</span></li>
                            <li><span>价格</span></li>
                            <li><span>人气</span></li>
                        </ul>
                    </div>	
                </header>	

                <section style={{marginTop:"90px",paddingBottom:"4em"}}>
                    
                    <div>
                        <div className="tabSlider-wrap" style={{paddingBottom:"10px"}}>
                            <div className="tabSlider-box curr">
                                <ul className="plist">
                                {
                                        list.map((item,index)=>(
                                            <li
                                            key={index}
                                            data={item}
                                            
                                            ><img src={"http://localhost:3000"+item.ico} style={{width:"100%",height:"120px"}} />
                                            <p className="name">{item.title}</p>
                                            <div style={{position:"relative"}}>
                                                <p style={{fontSize:"16px",color:"#ed0122"}}>￥200.00</p>
                                                            
                                                    <del style={{marginLeft:"4px"}}>￥300.00</del>
                                                    <span style={{position:"absolute",bottom:"0px",right:"5px",color:"red"}}>
                                                        <i className="icon-homeSmaill">&#xe015;</i>							
                                                    </span>
                                            </div>
                                        </li>
                                        ))
                                    }
                                  
                                    
                                </ul>
                            </div>
                            {/* <div class="tabSlider-box">
                                123
                            </div> */}
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default Product

