import React from 'react';
import {Route, Redirect} from 'react-router-dom'


import axios from 'axios';

class AuthUser extends React.Component{
  constructor(){
    super();

    this.state={
      hasAuth:false,//是否发送过授权请求
      error:1,//授权结果
      data:{}
    };

    //axios
    axios({
      url:'/api/user'
    }).then(
      res=>{
        this.setState({hasAuth:true,error:res.data.error,data:res.data.data})
      }
    )
  }
  render(){
    if (!this.state.hasAuth) return null;

    let {component:Component, ...rest} = this.props;

    return <Route {...rest} render={(rest)=>(
      this.state.error === 0 ? <Component props={rest} data={this.state.data} /> : <Redirect to="/login"/>
    )} />
  }
}
export default AuthUser;